import React, { Component } from 'react'
import Newsitem from './Newsitem'
import Spinner from './Spinner';
import PropTypes from 'prop-types'
import InfiniteScroll from "react-infinite-scroll-component";


export  class News extends Component {

    static defaultProps ={
        country:"in",
        pageSize:5,
        category:"general"
    }

    static propTypes={
        country: PropTypes.string,
        pageSize:PropTypes.number,
        category:PropTypes.string
    }

    capitalizedFirstLetter=(string)=>{
        return string.charAt(0).toUpperCase() + string.slice(1);
    }



    constructor(props){
        super(props);
        this.state=
        {articles: [],
        loading: false,
        page:1,
        totalResults:0
        }
        document.title=`NewsExpress - ${this.capitalizedFirstLetter(this.props.category)}`;
    }

    async componentDidMount(){

        this.props.setProgress(10);
        let  url=`https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=${this.props.apiKey}&page=${this.state.page}&pageSize=${this.props.pageSize}`;
        this.setState({loading:true});
        let data= await fetch(url);
        let parsedData=await data.json();
        this.setState({articles: parsedData.articles, 
            totalResults: parsedData.totalResults,
            loading:false})
            this.props.setProgress(100);
    }


    handlePrevClick=async ()=>{
        
      let url=`https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=${this.props.apiKey}&page=${this.state.page - 1} &pageSize=${this.props.pageSize}`;
        this.setState({loading:true})
        let data= await fetch(url);
        let parsedData=await data.json();
   
        this.setState({
            page:this.state.page - 1,
            articles: parsedData.articles,
            loading:false,
            totalResults: parsedData.totalResults
            
        })
        

    }

    handleNextClick=async ()=>{
        if(!(this.state.page + 1 > Math.ceil(this.state.totalResults/this.props.pageSize)))
      {
        let url=`https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=${this.props.apiKey}&page=${this.state.page + 1} &pageSize=${this.props.pageSize}`;
        this.setState({loading:true})
        let data= await fetch(url);
        let parsedData=await data.json();
      
        this.setState({
            page:this.state.page + 1,
            articles: parsedData.articles,
            loading:false,
            totalResults: parsedData.totalResults
            
        })
    
    }}
    
  fetchMoreData = async() => {
        
          this.setState({
            page: this.state.page + 1})
            const url=`https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=${this.props.apiKey}&page=${this.state.page}&pageSize=${this.props.pageSize}`;
        this.setState({loading:true});
        let data= await fetch(url);
        let parsedData=await data.json();
        this.setState({
            articles:this.state.articles.concat(parsedData.articles), 
            totalResults: parsedData.totalResults,
            loading:false,
        })
    
          };

    render() {
        return (
            <>
                <h1 className="text-center mt-5 padding:12px   ">
               NewsExpress - Top  {this.capitalizedFirstLetter(this.props.category)} Headlines
                </h1>
             

             <InfiniteScroll
          dataLength={this.state.articles.length}
          next={this.fetchMoreData}
          hasMore={this.state.articles.length !==this.state.totalResults}
          loader={<Spinner/>}
        >
            <div className="container">
                <div className="row">

                {this.state.articles.map((element)=>{
                    return (
                    <div className="col-md-4" key={element.url}>
                    <Newsitem  title={element.title} description={element.description} imageUrl={element.urlToImage} newsUrl={element.url} author={element.author} date={element.publishedAt} source={element.source.name}/>
                    </div>
                    )
                
                })}
                
                
               
            </div>
            </div>
            </InfiniteScroll>
            
            </> 
            
        )
    }
}


export default News;
